/* Обертка для всего экрана */
.full-screen-container {
    position: relative; /* Для позиционирования элементов */
    width: 100%; /* Занимает всю ширину экрана */
    height: 100vh; /* Занимает всю высоту экрана */
    overflow: hidden; /* Скрываем выходящие за пределы экрана элементы */
    user-select: none; /* Запрещаем выделение текста <button class="citation-flag" data-index="7"> */
    background: linear-gradient(-45deg, #1a1a2e, #16213e, #462066, #1a1a2e);
    background-size: 400% 400%;
    animation: gradientBG 15s ease infinite;
}

@keyframes gradientBG {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}

/* Добавляем анимированное сердце на фоне */
.background-heart {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 50vmin;
    height: 50vmin;
    opacity: 0.1;
    z-index: 0;
    pointer-events: none;
}

.background-heart::before,
.background-heart::after {
    content: '';
    position: absolute;
    top: 0;
    width: 50%;
    height: 100%;
    background: #ff1493;
    border-radius: 50% 50% 0 0;
    transform-origin: 100% 100%;
    animation: heartbeat 2s ease-in-out infinite;
}

.background-heart::before {
    left: 50%;
    transform: rotate(-45deg);
}

.background-heart::after {
    left: 0;
    transform: rotate(45deg);
}

/* Обновляем стиль фонового текста */
.background-text {
    position: fixed;
    top: 65%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 4vmin;
    color: rgba(255, 255, 255, 0.15);
    white-space: nowrap;
    z-index: 0;
    pointer-events: none;
    font-family: 'Arial', sans-serif;
    text-transform: uppercase;
    letter-spacing: 0.8em;
    font-weight: bold;
    animation: textGlow 4s ease-in-out infinite alternate;
}

@keyframes textGlow {
    0%, 100% {
        opacity: 0.15;
        text-shadow: 0 0 15px rgba(255, 255, 255, 0.3);
    }
    50% {
        opacity: 0.2;
        text-shadow: 0 0 30px rgba(255, 255, 255, 0.5);
    }
}

/* Удаляем старые стили */
.toggle-content-button,
.content-container {
    display: none;
}

/* Обновляем стиль кнопки возврата в меню */
.menu-button {
    position: fixed;
    top: 20px;
    left: 50%;
    transform: translateX(-50%);
    padding: 10px 20px; /* Уменьшаем padding */
    min-width: 150px; /* Уменьшаем минимальную ширину */
    height: auto; /* Убираем фиксированную высоту */
    font-size: 0.9em; /* Уменьшаем размер шрифта */
    background: rgba(30, 30, 30, 0.9);
    border: 2px solid rgba(255, 255, 255, 0.1);
    border-radius: 12px;
    color: #ff1493;
    cursor: pointer;
    transition: all 0.3s ease;
    z-index: 100;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
    display: flex;
    align-items: center;
    justify-content: center;
    text-transform: uppercase;
    letter-spacing: 1.5px;
    font-weight: 700; /* Делаем шрифт жирнее */
    white-space: nowrap; /* Предотвращаем перенос текста */
}

.menu-button::before {
    content: '↩';
    font-size: 1.2em;
    margin-right: 8px; /* Добавляем отступ между стрелкой и текстом */
}

.menu-button:hover {
    background: rgba(40, 40, 40, 0.95);
    transform: translateX(-50%) translateY(-2px);
    box-shadow: 0 8px 25px rgba(255, 20, 147, 0.2);
    color: #ff6b6b;
}

/* Обновляем позицию счетчика очков */
.score-counter {
    position: fixed;
    top: 20px;
    left: 20px; /* Возвращаем влево */
    font-size: 1.4rem;
    font-weight: 600;
    letter-spacing: 0.3px;
    color: #ff1493;
    background: rgba(30, 30, 30, 0.9);
    backdrop-filter: blur(8px);
    border: 2px solid rgba(255, 255, 255, 0.1);
    padding: 10px 20px;
    border-radius: 12px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
    z-index: 100;
    display: flex;
    align-items: center;
    gap: 10px;
    cursor: pointer;
    transition: all 0.3s ease;
}

.score-counter:hover {
    transform: translateY(-2px);
    box-shadow: 0 8px 25px rgba(255, 20, 147, 0.2);
    background: rgba(40, 40, 40, 0.95);
}

.score-counter .score-number {
    font-weight: 700;
    color: #ff1493;
    text-shadow: none;
    font-size: 1.05em;
}

/* Анимация пульсации для счетчика */
.score-number {
    display: inline-block;
    animation: pulse 2s infinite;
}

@keyframes pulse {
    0% { transform: scale(1); }
    50% { 
        transform: scale(1.1);
        text-shadow: 0 0 15px rgba(255, 20, 147, 0.5);
    }
    100% { transform: scale(1); }
}

/* Иконка стрелочки */
.arrow-icon {
    font-size: 1rem;
    margin-left: 10px;
}

/* Улучшенное подменю */
.sub-menu {
    position: absolute;
    top: 60px;
    left: 0;
    background: rgba(30, 30, 30, 0.95);
    backdrop-filter: blur(12px);
    border: 2px solid rgba(255, 20, 147, 0.1);
    border-radius: 20px;
    padding: 15px;
    min-width: 320px;
    z-index: 101;
    color: rgba(255, 255, 255, 0.9);
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.5);
    opacity: 0;
    transform: translateY(-10px);
    pointer-events: none;
}

.sub-menu.show {
    animation: menuAppear 0.4s cubic-bezier(0.4, 0, 0.2, 1) forwards;
    pointer-events: auto;
}

.sub-menu.closing {
    opacity: 0;
    transform: translateY(-10px);
    pointer-events: none;
}

/* Обновляем стили для всех элементов подменю */
.sub-menu p {
    margin: 0;
    padding: 8px 0;
}

.sub-menu p:first-child {
    color: rgba(255, 255, 255, 0.7);
    font-size: 0.85em;
    font-weight: normal;
    margin-bottom: 10px;
    letter-spacing: 0.3px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

/* Обновляем стили для текста в подменю */
.sub-menu .text {
    padding: 8px 12px;
    margin: 5px 0;
    background: rgba(40, 40, 40, 0.5);
    border-radius: 10px;
    transition: all 0.3s ease;
    font-weight: 600;
    font-size: 0.9em;
    letter-spacing: 0.3px;
    line-height: 1.2;
    display: flex;
    gap: 5px; /* Маленький отступ между элементами */
    align-items: center;
    color: #ff1493;
}

.sub-menu .text:hover {
    background: rgba(50, 50, 50, 0.7);
    transform: translateX(5px);
}

.sub-menu .highlight {
    font-size: 0.95em;
    font-weight: 700;
    color: #ff1493;
    padding: 0 3px;
    text-shadow: none;
}

/* Обновляем стили для текста внутри контейнера */
.content-container .title {
    font-size: 2.5em;
    margin-bottom: 20px;
    background: linear-gradient(45deg, #ff6b6b, #ff1493, #ff6b6b);
    background-size: 200% auto;
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    animation: gradient 3s linear infinite;
}

.content-container .text {
    font-size: 1.1rem;
    line-height: 1.4;
    color: rgba(255, 255, 255, 0.9);
    margin: 10px 0;
    padding: 0 20px;
}

/* Добавляем декоративные элементы */
.content-container::before,
.content-container::after {
    content: '❤️';
    position: absolute;
    font-size: 2em;
    opacity: 0.5;
    animation: float 3s ease-in-out infinite;
}

.content-container::before {
    top: -20px;
    left: -20px;
    animation-delay: 0s;
}

.content-container::after {
    bottom: -20px;
    right: -20px;
    animation-delay: 1.5s;
}

@keyframes float {
    0%, 100% {
        transform: translateY(0);
    }
    50% {
        transform: translateY(-10px);
    }
}

/* Обновляем анимацию для кнопок */
.navigation-buttons {
    margin-top: auto;
    padding-top: 20px;
    display: flex;
    justify-content: center;
    gap: 20px;
    opacity: 0;
    transform: translateY(20px);
}

.navigation-buttons.animate {
    animation: buttonsAppear 0.8s ease-out forwards;
}

@keyframes buttonsAppear {
    from {
        opacity: 0;
        transform: translateY(20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.nav-button {
    padding: 12px 25px;
    min-width: 160px;
    border: none;
    border-radius: 25px;
    background: linear-gradient(45deg, #ff6b6b, #ff1493);
    color: white;
    font-size: 0.9rem;
    cursor: pointer;
    transition: all 0.3s ease;
    text-transform: uppercase;
    letter-spacing: 1px;
    box-shadow: 0 5px 15px rgba(255, 20, 147, 0.3);
}

.nav-button:hover {
    transform: translateY(-2px);
    box-shadow: 0 8px 20px rgba(255, 20, 147, 0.4);
    background: linear-gradient(45deg, #ff1493, #ff6b6b);
}

.nav-button:active {
    transform: translateY(0);
}

/* Улучшенные стили текста */
.title {
    font-size: 3em;
    font-weight: bold;
    background: linear-gradient(45deg, #ff6b6b, #ff1493);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    margin-bottom: 20px;
    animation: titleFloat 3s ease-in-out infinite;
}

@keyframes titleFloat {
    0%, 100% { transform: translateY(0); }
    50% { transform: translateY(-10px); }
}

/* Стиль текста */
.text {
    font-size: 1.2rem;
    font-weight: 500;
    color: #333; /* Темно-серый цвет */
    margin: 10px 0;
    line-height: 1.6;
}

/* Выделение важных слов */
.highlight {
    position: relative;
    display: inline-block;
    background: linear-gradient(45deg, #ff6b6b, #ff1493);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    animation: highlightGlow 2s ease-in-out infinite;
}

@keyframes highlightGlow {
    0%, 100% {
        filter: drop-shadow(0 0 2px rgba(255, 20, 147, 0.5));
    }
    50% {
        filter: drop-shadow(0 0 5px rgba(255, 20, 147, 0.8));
    }
}

/* Улучшенные падающие элементы */
.falling-heart,
.falling-special-heart,
.falling-bomb {
    position: absolute;
    width: 50px;
    height: auto;
    cursor: pointer;
    will-change: transform;
    z-index: 1;
}

/* Анимация для обычных сердечек */
.falling-heart {
    animation: fall 3s linear forwards;
    filter: drop-shadow(0 0 5px rgba(255, 20, 147, 0.5));
}

/* Анимация для особых сердечек */
.falling-special-heart {
    animation: fall 4s linear forwards;
    filter: drop-shadow(0 0 8px rgba(255, 20, 147, 0.8));
}

/* Анимация для бомб */
.falling-bomb {
    width: 80px; /* Отдельный размер для бомб */
    animation: fall 3.5s linear forwards;
    filter: drop-shadow(0 0 5px #ff4500);
}

@keyframes fall {
    from {
        top: -50px;
        transform: translateY(0) rotate(0deg);
        opacity: 1;
    }
    to {
        top: calc(100vh + 50px);
        transform: translateY(0) rotate(360deg);
        opacity: 0;
    }
}

/* Стиль особенных сердечек */
.falling-special-heart {
    animation: fall 4s linear forwards;
}

/* Стиль бомб */
.falling-bomb {
    animation: fall 3.5s linear forwards;
}

/* Улучшенные сообщения */
.love-message {
    position: absolute;
    font-size: 1.2rem;
    font-weight: bold;
    background: linear-gradient(45deg, #ff6b6b, #ff1493);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);
    animation: messageFloat 1.5s ease-out forwards;
    z-index: 2; /* Сообщения должны быть над сердечками, но под интерфейсом */
    pointer-events: none;
}

@keyframes messageFloat {
    0% {
        opacity: 0;
        transform: scale(0.8) translateY(0);
    }
    20% {
        opacity: 1;
        transform: scale(1.1) translateY(-10px);
    }
    80% {
        opacity: 1;
        transform: scale(1) translateY(-20px);
    }
    100% {
        opacity: 0;
        transform: scale(0.9) translateY(-30px);
    }
}

/* Стиль таймера */
.timer {
    position: absolute;
    top: 30%;
    left: 105%;
    margin-right: 50px; /* Добавляем отступ справа */
    font-size: 1.2rem;
    font-weight: bold;
    color: #ff1493; /* Ярко-розовый цвет */
    background-color: rgba(30, 30, 30, 0.9);
    padding: 5px 10px; /* Внутренние отступы */
    border-radius: 10px; /* Закругленные углы */
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
    z-index: 4; /* Размещаем поверх элементов */
    background: rgba(30, 30, 30, 0.9);
    backdrop-filter: blur(4px);
    border: 2px solid rgba(255, 255, 255, 0.1);
    animation: timerPulse 2s ease-in-out infinite;
}

@keyframes timerPulse {
    0%, 100% {
        box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
    }
    50% {
        box-shadow: 0 8px 32px 0 rgba(255, 20, 147, 0.5);
    }
}

/* Добавляем медиа-запросы для адаптивности */
@media (max-width: 768px) {
    /* Удаляем старые стили */
    .toggle-content-button,
    .content-container {
        display: none;
    }

    /* Обновляем стили для мобильных устройств */
    .menu-button {
        min-width: 130px;
        padding: 8px 16px;
        font-size: 0.8em;
    }

    .score-counter {
        font-size: 1.2rem;
        padding: 8px 15px;
        left: 15px; /* Обновляем отступ для мобильных */
    }

    /* Таймер */
    .timer {
        position: absolute;
        top: 100%;
        left: 0;
        margin-top: 10px;
        width: 100%;
        text-align: center;
        background-color: rgba(30, 30, 30, 0.9);
    }

    /* Подменю */
    .sub-menu {
        min-width: 280px;
        left: 0;
        font-size: 0.9rem;
        white-space: normal;
    }

    /* Кнопка сворачивания */
    .toggle-content-button {
        top: 2%;
        left: 50%;
        transform: translateX(-50%);
        font-size: 0.9rem;
        padding: 8px 15px;
    }

    /* Контейнер с контентом */
    .content-container {
        width: 95%;
        padding: 20px;
    }

    .content-container .text {
        font-size: 1rem;
        padding: 0 10px;
    }

    .navigation-buttons {
        flex-direction: row;
        gap: 10px;
    }

    .title {
        font-size: 1.8em;
        margin-bottom: 15px;
    }

    .text {
        font-size: 1rem;
        margin: 8px 0;
    }

    /* Падающие элементы */
    .falling-heart,
    .falling-special-heart {
        width: 35px;
    }

    .falling-bomb {
        width: 50px;
    }

    /* Сообщения */
    .love-message {
        font-size: 1rem;
    }

    /* Кнопка с котом */
    .cat-gif {
        width: 80px;
    }

    .achievements-button {
        right: 15px;
        width: 50px;
        height: 50px;
        font-size: 1.8em;
    }
}

/* Дополнительные стили для очень маленьких экранов */
@media (max-width: 380px) {
    .score-counter {
        font-size: 1rem;
        padding: 6px 12px;
    }

    .title {
        font-size: 1.5em;
    }

    .text {
        font-size: 0.9rem;
    }

    .falling-heart,
    .falling-special-heart {
        width: 30px;
    }

    .falling-bomb {
        width: 45px;
    }

    .cat-gif {
        width: 60px;
    }

    .sub-menu {
        font-size: 0.8rem;
    }
}

/* Исправляем ориентацию для ландшафтного режима */
@media (max-height: 600px) and (orientation: landscape) {
    .content-container {
        top: 60%;
        max-height: 60vh;
    }

    .title {
        font-size: 1.5em;
        margin-bottom: 10px;
    }

    .text {
        font-size: 0.9rem;
        margin: 5px 0;
    }

    .score-counter {
        top: 5%;
    }

    .toggle-content-button {
        top: 5%;
    }
}

/* Добавляем мета-тег для корректного масштабирования */

/* Добавляем контейнер для частиц */
.particles {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    z-index: 1;
}

.particle {
    position: absolute;
    width: 10px;
    height: 10px;
    background: rgba(255, 255, 255, 0.5);
    border-radius: 50%;
    pointer-events: none;
    animation: particleFloat 15s infinite linear;
}

@keyframes particleFloat {
    0% {
        transform: translateY(0) translateX(0) rotate(0deg);
        opacity: 0;
    }
    50% {
        opacity: 0.5;
    }
    100% {
        transform: translateY(-100vh) translateX(100px) rotate(360deg);
        opacity: 0;
    }
}

/* Добавляем эффект пузырьков для фона */
.bubble {
    position: absolute;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 50%;
    animation: bubbleRise 20s infinite ease-in;
    pointer-events: none;
}

@keyframes bubbleRise {
    0% {
        transform: translateY(100vh) scale(0);
        opacity: 0;
    }
    50% {
        opacity: 0.5;
    }
    100% {
        transform: translateY(-100vh) scale(1);
        opacity: 0;
    }
}

/* Обновляем стили для анимации текста */
.typing-text {
    opacity: 0;
    transform: translateY(20px);
    margin: 0 auto;
    position: relative;
    width: 100%;
}

.typing-text.animate {
    animation: fadeInUp 1s ease forwards;
}

@keyframes fadeInUp {
    from {
        opacity: 0;
        transform: translateY(20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

/* Обновляем задержки для текста */
.typing-text.animate:nth-child(1) { 
    animation-delay: 0.3s;
}
.typing-text.animate:nth-child(2) { 
    animation-delay: 1s;
}
.typing-text.animate:nth-child(3) { 
    animation-delay: 1.7s;
}
.typing-text.animate:nth-child(4) { 
    animation-delay: 2.4s;
}

/* Обновляем стили для highlight */
.typing-text .highlight {
    opacity: 0;
    transform: scale(0.9);
    display: inline-block;
}

.typing-text.animate .highlight {
    animation: popIn 0.5s ease forwards;
    animation-delay: 0.2s;
}

@keyframes popIn {
    from {
        opacity: 0;
        transform: scale(0.9);
    }
    to {
        opacity: 1;
        transform: scale(1);
    }
}

/* Добавляем стили для контейнера текста */
.content-container .text,
.content-container .title {
    color: rgba(255, 255, 255, 0.9);
}

.beating-heart {
    animation: heartbeat 1s infinite;
}

@keyframes heartbeat {
    0% { transform: scale(1); }
    25% { transform: scale(1.1); }
    50% { transform: scale(1); }
    75% { transform: scale(1.1); }
    100% { transform: scale(1); }
}

.bonus-mode-indicator {
    position: fixed;
    top: 20%;
    left: 50%;
    transform: translateX(-50%);
    background: rgba(255, 20, 147, 0.2);
    padding: 10px 20px;
    border-radius: 20px;
    color: #ff1493;
    font-weight: bold;
    animation: pulse 1s infinite;
    z-index: 100;
}

.combo-text {
    font-size: 2em;
    color: #ff1493;
    text-shadow: 0 0 10px rgba(255, 20, 147, 0.5);
    animation: comboScale 0.5s ease-out;
}

@keyframes comboScale {
    0% { transform: scale(0.5); opacity: 0; }
    50% { transform: scale(1.2); }
    100% { transform: scale(1); opacity: 1; }
}

.love-message.achievement {
    font-size: 1.5em;
    background: linear-gradient(45deg, #FFD700, #FFA500);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    animation: achievementFloat 3s ease-out forwards;
}

@keyframes achievementFloat {
    0% {
        opacity: 0;
        transform: scale(0.8) translateY(0);
    }
    20% {
        opacity: 1;
        transform: scale(1.2) translateY(-20px);
    }
    80% {
        opacity: 1;
        transform: scale(1.2) translateY(-20px);
    }
    100% {
        opacity: 0;
        transform: scale(1) translateY(-40px);
    }
}

/* Кнопка достижений */
.achievements-button {
    position: fixed;
    top: 20px;
    right: 20px;
    margin-right: 15px; /* Добавляем отступ справа */
    font-size: 2em;
    background: rgba(30, 30, 30, 0.9);
    border: 2px solid rgba(255, 255, 255, 0.1);
    border-radius: 50%;
    width: 60px;
    height: 60px;
    cursor: pointer;
    transition: all 0.3s ease;
    z-index: 100;
    backdrop-filter: blur(5px);
