    animation: notificationPop 0.3s ease forwards, 
               notificationFloat 2.7s ease forwards;
}

@keyframes notificationPop {
    0% {
        transform: scale(0.8);
        opacity: 0;
    }
    50% {
        transform: scale(1.1);
    }
    100% {
        transform: scale(1);
        opacity: 1;
    }
}

@keyframes notificationFloat {
    0% {
        transform: translateY(0);
        opacity: 1;
    }
    80% {
        transform: translateY(-10px);
        opacity: 1;
    }
    100% {
        transform: translateY(-20px);
        opacity: 0;
    }
}

@keyframes slideIn {
    from {
        transform: translateY(-100%);
        opacity: 0;
    }
    to {
        transform: translateY(0);
        opacity: 1;
    }
}

@keyframes slideOut {
    from {
